export class PersonalData {
  Name: string;
  Age: number;
  Gender: string;
  HighRiskGroup: boolean;
}

export class Location {
  Hospital: string;
  Department: string;
  RoomNumber: number;
  BedNumber: number;
  BedLocation: string;
}


export class HospitalizationInfo {
  ReceptionTime;
  ReleaseTime;
  HospitalizationStatus;
  Comments;
}

export class PersonSettings {
  // settings info
  minHR: number;
  maxHR: number;
  minBR: number;
  maxBR: number;
  minSpO2: number;
  maxSpO2: number;
  sensors;
}

export class AlertData {
  name: string;
  time: Date;
}

export class PersonHistory {
  alerts: AlertData[];
  scoring: number[];
}

export class PersonHealthData {
  PatientID;
  personal: PersonalData;
  location: Location;
  hospitalization: HospitalizationInfo;
  //health data
  heartRate: number;
  bloodPresure;
  spO2:number;
  breathingRate:number;
  extraO2;
  fever;
  breathingInfo;
  alerts;
  progress;
  score:number;
  history: PersonHistory;
  settings: PersonSettings;
}

